<template>
  <div>
    <div class="add-button-container">
      <button class="custom-card-button" @click="addCard">
        <i class="fas fa-plus"></i> Tambah
      </button>
    </div>

    <div class="d-flex justify-content-center mt-5">
      <div v-for="(card, index) in cards" :key="index" class="custom-card">
        <div class="custom-card-content">
          <div class="custom-card-image">
            <img :src="card.gambar" alt="Profile Picture" @click="openImageInput(card, index)" />
            <input type="file" accept="image/*" @change="() => uploadImage(index)" :ref="'imageInput_' + index" style="display: none" />
          </div>

          <div class="custom-card-details">
            <form>
              <div class="title">
                <h3 v-if="!card.editing">{{ card.nama_kegiatan }}</h3>
                <input v-else v-model="card.nama_kegiatan" class="custom-input" placeholder="Nama" :required="card.editing" :style="{'display': card.editing ? 'block' : 'none'}"/>
                <div v-if="!card.editing" class="date-and-period-container"> 
                  <div class="left-align">
                    <h6 >Tanggal: {{ formatDate(card.tanggal) }}</h6>
                  </div>
                  <div class="right-align">
                    <h6> Periode: {{ card.periode }}</h6>
                  </div>
                </div>
                <input v-else type="date" v-model="card.tanggal" class="custom-input" placeholder="Tanggal" @change="updatePeriode(index)" :required="card.editing">

                <table class="kategori-and-tempat">
                  <tbody>
                    <!-- <tr>
                      <td>Tanggal</td>
                      <td>:</td>
                      <td v-if="!card.editing">{{ formatDate(card.tanggal) }}</td>
                      <td v-else><input type="date" v-model="card.tanggal" class="custom-input" placeholder="Tanggal" @change="updatePeriode(index)" :required="card.editing" /></td>
                    </tr>
                    <tr>
                      <td>Periode</td>
                      <td>:</td>
                      <td v-if="!card.editing">{{ card.periode }}</td>
                      <td v-else><input v-model="card.periode" class="custom-input" placeholder="Periode" :required="card.editing" /></td>
                    </tr> -->
                    <tr>
                      <td>Kategori</td>
                      <td>:</td>
                      <td v-if="!card.editing">{{ card.kategori }}</td>
                      <td v-else><input v-model="card.kategori" class="custom-input" placeholder="Kategori" :required="card.editing" /></td>
                    </tr>
                    <tr>
                      <td>Tempat</td>
                      <td>:</td>
                      <td v-if="!card.editing">{{ card.tempat }}</td>
                      <td v-else><input v-model="card.tempat" class="custom-input" placeholder="Tempat Kegiatan" :required="card.editing" /></td>
                    </tr>
                  </tbody>
                </table>
              </div>

              <hr>
              <p v-if="!card.editing">{{ card.keterangan }}</p>
              <textarea v-else v-model="card.keterangan" class="custom-input" placeholder="Keterangan Kegiatan" :required="card.editing"></textarea>

              <div class="button-card">
                <button class="custom-card-button" v-if="!card.editing" @click="toggleEditMode(index)">
                  <i class="fas fa-edit"></i> Edit
                </button>
                <button class="custom-card-button" v-else @click="saveCard(index)">
                  <i class="fas fa-save"></i> Save
                </button>
                <button class="delete-button" @click="deleteCard(index)">
                  <i class="fas fa-trash-alt"></i> Delete
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      cards: [
        {
          nama_kegiatan: '',
          tanggal: '',
          periode: '',
          tempat: '',
          keterangan: '',
          gambar: '',
          kategori: '',
          editing: false,
        }
      ],
    };
  },

  mounted() {
    this.fetchCards();
  },

  methods: {
    updatePeriode(index) {
      const card = this.cards[index];
      const selectedDate = new Date(card.tanggal);

      if (!isNaN(selectedDate.getFullYear())) {
        const nextYear = selectedDate.getFullYear() + 1;
        card.periode = `${selectedDate.getFullYear()}/${nextYear}`;
      }
    },

    async fetchCards() {
      try {
        const response = await axios.get('https://siswaapi.gkiswjateng.org/api/kegiatan');
        this.cards = response.data;
      } catch (error) {
        console.error('Error fetching cards:', error);
      }
    },

    async deleteCard(index) {
      try {
        const card = this.cards[index];

        if (card.id) {
          const response = await axios.delete(`https://siswaapi.gkiswjateng.org/api/kegiatan/${card.id}`);

          if (response.status === 200) {
            this.cards.splice(index, 1);
          } else {
            console.error(`Error deleting card: ${response.status}`);
          }
        }
      } catch (error) {
        console.error('Error deleting card:', error);
      }
    },

    addCard() {
      this.cards.unshift({
        nama_kegiatan: '',
        tanggal: '',
        periode: '',
        tempat: '',
        keterangan: '',
        gambar: '',
        kategori: '',
        editing: false,
      });
    },

    toggleEditMode(index) {
      const card = this.cards[index];

      if (card) {
        if (card.editing) {
          if (!card.nama_kegiatan || !card.tanggal || !card.periode || !card.tempat || !card.keterangan || !card.gambar || !card.kategori) {
            card.errorMessage = 'Silakan isi semua kolom.';
            return;
          }
        }

        card.editing = !card.editing;
      }
    },

    async saveCard(index) {
      const card = this.cards[index];
      card.errorMessage = '';

      try {
        const response = await axios[card.id ? 'put' : 'post'](
          card.id ? `https://siswaapi.gkiswjateng.org/api/doa/${card.id}` : 'https://siswaapi.gkiswjateng.org/api/doa',
          card
        );
        this.$set(this.cards, index, { ...card, ...response.data });
        card.editing = false;
      } catch (error) {
        console.error('Error saving card:', error);

        if (error.response && error.response.data) {
          const serverErrorMessage = error.response.data.message;
          card.errorMessage = `Error: ${serverErrorMessage}`;
        } else {
          card.errorMessage = 'Error saving data.';
        }
      }
    },
    formatDate(dateString) {
      const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', options);
    },

    uploadImage(index) {
      const card = this.cards[index];

      if (card.editing) {
        const inputElement = this.$refs['imageInput_' + index][0];

        if (inputElement && inputElement.files && inputElement.files.length > 0) {
          const file = inputElement.files[0];

          const reader = new FileReader();

          reader.onload = (e) => {
            card.gambar = e.target.result;
            card.imageChanged = true;
          };

          reader.readAsDataURL(file);
        } else {
          console.error(`Input element for index ${index} is undefined or has no selected file.`);
        }
      }
    },

    openImageInput(card, index) {
      if (card.editing) {
        const inputElement = this.$refs['imageInput_' + index][0];

        if (inputElement) {
          inputElement.click();
        } else {
          console.error(`Input element for index ${index} is undefined.`);
        }
      }
    },
  }
};
</script>

